import React from 'react';

const TYPING_SPEED = 150;
const DELETING_SPEED = 30;

class Typer extends React.Component {

  state = {
    elementId:'',
    text: '',
    isDeleting: false,
    loopNum: 0,
    typingSpeed: TYPING_SPEED,
    searchContent:sessionStorage.getItem('emsebach') ?? '',
    typingTimer: null
  }

  componentDidMount() {
    this.handleType();
  }

  handleType = () => {
    const { dataText, dataElementId } = this.props;
    const { isDeleting, loopNum, text, typingSpeed } = this.state;
    const i = loopNum % dataText.length;
    const fullText = dataText[i];

    this.setState({
      text: isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1),
      typingSpeed: isDeleting ? DELETING_SPEED : TYPING_SPEED,
      elementId : dataElementId
    });

    if (!isDeleting && text === fullText) {     
      setTimeout(() => this.setState({ isDeleting: true }), 500);   
    } else if (isDeleting && text === '') {
      this.setState({
        isDeleting: false,
        loopNum: loopNum + 1
      });      
    }

    setTimeout(this.handleType, typingSpeed);
  };

handleChange = (event) => {
    event.persist();
    sessionStorage.setItem('emsebach', event.target.value)
    if(event.target.value)
    this.props.navigate(`/search?query=${event.target.value}`)
    this.setState({searchContent:event.target.value});
}

optimisedHandleChange = (event) => {

  clearTimeout(this.state.typingTimer)
  let typingTimer = setTimeout(() => {this.handleChange(event)}, 1000);
  this.setState({typingTimer});
} 
render() {
    return (
        <>
        <div className={`${this.state.elementId == 'mobileSearchBar' ? 'col-sm-12 p-2 mobileSearchContainer' : this.state.elementId}`}>
        <form className={`nosubmit ${this.state.elementId}`} onSubmit={ (e) => { e.preventDefault(); }}>
        <input className="nosubmit" autoFocus={this.props.autoFocus} defaultValue={this.props.searchValue} type="search" placeholder={ this.state.text } onKeyUp={(event)=>this.optimisedHandleChange(event)} />
        </form> 
        </div> 
        </>
    );
  }
}

export default Typer;
